body {
  margin: 0;
  padding: 0;
  font-family: 'Arial', sans-serif;
  background-color: #252422; /* Dark background color */
  color: #fff; /* White text color */
  align-items: center;
  
}

nav {
  background-color: #403d39; /* Dark background color for the navigation bar */
  padding: 10px;

}

nav ul {
  list-style-type: none;
  margin: 10px;
  padding: 0;
}

nav ul li {
  display: inline-block;
  margin-left: 380px; /* Adjusted margin for better spacing */
}

nav ul li a {
  text-decoration: none;
  color: #fff; /* White text color for links */
  font-weight: bold;
  transition: color 0.3s ease; /* Smooth color transition on hover */
}

nav ul li:hover {
  cursor: pointer;
  text-decoration: underline;
}

nav ul li:hover a {
  color: #fc4300; /* Dark purple text color on hover */
}
