.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #252422;
  margin: 100px;
}

form {
  width: 600px;
  padding: 20px;
  border: 2px solid #eb5e28; /* Border color (orange) */
  border-radius: 8px;
  background-color: #403d39; /* Form background color */
  color: white;
  align-items: center;
  margin-top: 10px;
  margin-left: 450px;
  transition: background-color 0.3s ease;
}

form:hover {
  background-color: #f84408; /* Dark purple on hover */
  border-color:whitesmoke;
  border-width: 2px;
  color:black;
  
}
button {
  width: 100%;
  padding: 10px;
  margin-top: 10px;
  background-color: #f8f6f6; /* Button background color (orange) */
  color: rgb(18, 8, 8);
  border: 2px;
  border-color: black;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  color:#f8f6f6;
  background-color: #252422; /* Darker orange on hover */
}
