.user-card {
  border: 1px solid #ccc;
  padding: 10px;
  margin-bottom: 20px; /* Increased margin for better separation */
  transition: transform 0.3s ease;
}

.user-card:hover {
  transform: scale(1.02); /* Scale up on hover */
}

.placeholder-image {
  border: 1px solid #ccc;
  padding: 20px;
  margin-top: 20px;
  text-align: center;
  color: #666;
  transition: transform 0.3s ease;
}

.placeholder-image:hover {
  transform: scale(1.05); /* Scale up on hover */
}

.action-buttons {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}

.action-button {
  padding: 8px 12px;
  margin-right: 10px;
  cursor: pointer;
  border: none;
  border-radius: 4px;
  color: white;
  transition: background-color 0.3s ease;
}

.view-button {
  background-color: #4cc9f0; /* Light blue for view button */
}

.edit-button {
  background-color: #3f37c9; /* Dark purple for edit button */
}

.delete-button {
  background-color: #f72585; /* Pink for delete button */
}
